<template>
    <div class="w-full dark:text-[#d9d9d9]">
        <div class="max-w-7xl mx-auto h-full pt-10 pb-24">
            <!-- 히든 md 부터 블락 -->
            <h2 class="py-3 text-5xl  uppercase">Portfolio</h2>
            <div class="">
                <!-- mine Portfolio swiper -->
                <swiper :pagination="{type: 'fraction',}" :loop="true" :navigation="{prevEl:'.swiper-button-prev',nextEl:'.swiper-button-next'}" :modules="modules" class="content w-2/3">
                    <swiper-slide v-for="(e, index) in 3" :key="e">
                        <a href="#">
                            <img :src="require(`@/assets/images/img_0${index+1}.jpg`)" alt="e">
                        </a>
                    </swiper-slide>
                    <div class="swiper-button-prev"></div>
                    <div class="swiper-button-next"></div>
                </swiper>
            </div>
            <!-- 기본은 밀어서 하는 스와이퍼로 -->
        </div>
    </div>
</template>
<script>
  // Import Swiper Vue.js components
  import { Swiper, SwiperSlide } from 'swiper/vue';

  // Import Swiper styles
  import 'swiper/css';

  import 'swiper/css/pagination';
  import 'swiper/css/navigation';

  // import required modules
  import { Pagination, Navigation } from 'swiper';

export default {
    name:"HomePortfolio",
    components: {
      Swiper,
      SwiperSlide,
    },
    setup() {
      return {
        modules: [Pagination, Navigation],
      };
    },
}
</script>
<style >
.swiper {
  width: 90%;
  height: 90%;
}

.swiper-slide {
  text-align: center;
  background: #fff;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* .content .swiper-button-prev::after,
.content .swiper-button-next::after {
  display: none;
} */
</style>